nav {
	width: 100%;
	position: fixed;
	z-index: 1000;
}

nav ul {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  margin: 0;
  padding: 0;
}

nav ul li {
  list-style: none;
  display: flex;
  width: 25%;
}

nav ul li a{
	background-color: rgba(75,83,32,0.5);
	transition: 0.3s;
}

nav ul li a:hover {
	background-color: rgba(0,0,0,0.7);
	color:  white;
	text-shadow: 0 0 13px white;
}