main.page-main {
	width: 100%;
	height: 100vh;
	background-image: linear-gradient(to bottom, #157cc7,#157cc7 50%,#ff0 50%, #ff0);
	margin-top: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
}

div.main-img-container {
	width: 95%;
	height: 95vh;
	background: url('shev.jpg') no-repeat 20% 50%;
	background-size: cover;
	background-attachment: fixed;
}